.resume-link {
  display: flex;
  justify-content: center;
}
.keyword {
    color: white;
    text-shadow: 5px 5px 5px black;
    font-size: 32px;
}

.header-content {
    background-image: url(/portfolio-project/static/media/header-img1.873e4e9f.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 10px 10px 10px black;
}

.subtitle {
    margin: 0 10%;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    text-align: center;
    letter-spacing: 2px;
    font-size: 16px;
}

.title {
    width: 100%;
    font-size: 2em;
    font-family: 'Play', sans-serif;
    font-weight: 400;
    text-align: center;
}

h1, h3 {
    color: white;
    text-shadow: 2px 2px 2px black;
}

.letter {
    display: inline-block;
    line-height: 1em;
    transition: top .5s ease;
    text-align: center;
    width: 1em;
    height: 27em;
    position: relative;
}

.letter-ticker {
    height: 1em;
    overflow: hidden;
    position: relative;
}

@media only screen and (min-width: 768px) {
    .keyword {
        font-size: 48px;
    }
    .title {
        font-size: 3em;
    }
    .subtitle {
        font-size: 18px;
        margin: 0 20%;
    }
}

.test {
    border: 1px solid red;
}
p {
    text-align: justify;
    font-size: 16px;
    line-height: 1.5;
    color: var(--color-text)
}

/* p::first-letter {
    font-size: 250%;
    float: left;
    line-height: 1;
    margin: 0 .2em 0 0;
  } */

.extra {
    display: none;
}

.show-more-text {
    font-style: italic;
}
.section-title {
    color: var(--color-main);
}
.projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media only screen and (min-width: 996px) {
    .projects {
        flex-direction: row;
        justify-content: space-center
    }
}
.project-wrapper {
    position: relative;
    max-width: 250px;
    min-width: 250px;
    margin: 10px 10px;
    height: 250px;
    cursor: pointer;
    transform-style: preserve-3d;
    transition: all 300ms ease-in-out 300ms;
}

.project-wrapper-front {
    position: absolute;
    max-width: 100%;
    min-width: 100%;
    height:100%;
    box-shadow: 10px 10px 10px black;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(0deg);
}

.project-wrapper-back {
    position: absolute;
    max-width: 100%;
    min-width: 100%;
    height:100%;
    box-shadow: 10px 10px 10px black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(180deg);
}

img {
    opacity: .5;
    width: 250px; 
    height: 250px;
}
@media only screen and (min-width: 1200px) {
    .project-wrapper {
        max-width: 300px;
        min-width: 300px;
        height: 300px;
    }
    img {
        width: 300px; 
        height: 300px;
    }
    .flip {
        height: 100px;
    }
}

.flip {
    transform: rotateY(180deg);
    height: 350px;
}

.tile-title {
    position: absolute;
    z-index: 11;
    color: white;
    text-shadow: 2px 2px 2px black;
    width: 225px;
    text-align: center;
}

.link-list {
    padding-top: 10px;
    color: var(--color-text);
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.link {
    margin: 0 10px;
}

@media only screen and (min-width: 996px) {
    .link {
        margin: 0 30px;
    }
}
